<template>
  <div class="div-SearchBar">
  <div class="Search-handle">
    <div class="choose-div">
      <n-dropdown trigger="hover" :options="options" @select="handleSelect">
    <n-button class="choose-button" color="#6ac5ec">{{buttonText}}</n-button>
  </n-dropdown>
    </div>
    <div class="input-div">
      <input v-model="inputText" class="input-bar"/>
    </div>
    <div class="button-div">
        <n-button class="submit-button" @click="submitInput" color="#6ac5ec"></n-button>
      </div>
  </div>
  </div>


  <div class="search-div-book" v-show="showSearch">
          <div class="search-book-list">
          <div class="search-book-container"  v-for="book in booklist" :key="book.bookid">
            <div class="search-book-show">
                <div class="search-book-showleft"></div>
                <div class="search-book-showright">{{book.bookname}}</div>
            </div>
            <div class="search-book-show">
                <div class="search-book-showleft"></div>
                <div class="search-book-showright">{{book.bookid}}</div>
             </div>
            <div class="search-book-show">
                <div class="search-book-showleft"></div>
                <div class="search-book-showright">{{book.bookauthor}}</div>
            </div>
            <div class="search-book-show">
                <div class="search-book-showleft"></div>
                <div class="search-book-showright">{{book.bookpublisher}}</div>
            </div>
            <div class="search-book-show">
                <div class="search-book-showleft"></div>
            <div class="search-book-showright">{{book.booktag}}</div>
            </div>
            <div class="search-book-show">
              <div style="display:flex;justify-content:center;align-items:center; width:100%; height:100%;">
              <button style="height:30px; width:60px" @click="BorrowBook(book.bookid)"></button>
              </div>
            </div>
          </div>
          </div>
        </div>
</template>

<script>
import{defineComponent} from "vue";
import { useMessage } from 'naive-ui';
export default defineComponent({
name:'SearchBar',
props:{},
data(){return{
  buttonText:'',
  inputText:'',
  booklist:[],
  showSearch:false,
  message:useMessage()
}},
component:{},
watch:{
},
methods:{


  handleSelect(key){
      this.buttonText=key;
    },


  submitInput(){
   if(this.buttonText==''){
     this.searchName(this.inputText)
   }
   else{
     this.searchTag(this.inputText)
   }
  },
  BorrowBook(bookid){
    if(this.$store.state.isLogin==false){
      this.message.error('');
    }else(this.getBorrowBook(bookid))
  },


    //axios
    /****************************************************************/
    //ajax 
  searchName(Name){//get  Name 
    //ajax request: search bookName
    console.log(Name);
    this.booklist=[];
    {//axiosAJAX
      this.axios.get('book/find?bookName='+Name).then((response)=>{
        for(let i=0;i<response.data.length;i++)
        {
          this.booklist.push(
                {
                bookid:response.data[i].bookID,
                bookname:response.data[i].bookName,
                bookauthor:response.data[i].bookAuthorName,
                bookpublisher:response.data[i].bookPublisher,
                booktag:response.data[i].type,
                }
              )
        }
        console.log(response)

      }).catch((response)=>{
        console.log(response)
      })
    }
    //1.
    // 
    this.showSearch=true

    //2.
    // this.showSearch=false;//


    //

  },

  searchTag(Tag){//get  Type 
    //ajax request  search bookTag
    this.booklist=[];

        {//axiosAJAX
      this.axios.get('book/find?Type='+Tag).then((response)=>{
        for(let i=0;i<response.data.length;i++)
        {
          this.booklist.push(
                {
                bookid:response.data[i].bookID,
                bookname:response.data[i].bookName,
                bookauthor:response.data[i].bookAuthorName,
                bookpublisher:response.data[i].bookPublisher,
                booktag:response.data[i].type,
                }
              )
        }
        console.log(response)
        
      }).catch((response)=>{
        console.log(response)
      })
    }

 //1.
    // 
    this.showSearch=true

    //2.
      //this.showSearch=false;//


    //
  },
  getBorrowBook(bookid){//get  userid,bookid :boolean
        this.axios.post('borrow/add',
    {"cusID":this.$store.state.user.userid,
    "bookID":bookid,
    "borrowDate":Date.now(),
    "state":0}
    ).then((response)=>{
        console.log( response)
        if(response.data==1){
          this.message.success('!');
        }
        else{ this.message.error('!');}

      }).catch((response)=>{
        console.log( response)
        this.message.error('!');
      })
  }

    //
  },
setup(){
  return{
   options:[{label:"",key:""},
  {label:"",key:""}
    ],
  }
}
}
)
</script>

<style>
.div-SearchBar{
  display: flex;
  height:auto;
  width:100%;
  justify-content: center;
}
.Search-handle{
  margin-top:10px;
  display:flex;
  height:40px;
  width:480px;
  justify-content: center;
  align-items: center;
}
.choose-div{
height:40px;
width:60px;
display:flex;
justify-content: center;
align-items: center;
}

.choose-button{
  height:40px;
width:60px;
display:flex;
justify-content: center;
align-items: center;
}

.input-div{
height:40px;
width:300px;
padding-left:20px;
}
.input-bar{
  height:38px;
width:300px;
padding:0;
padding-left:10px;
border:1px solid #b0b3b8;

}

.button-div{
margin-left:10px;
height:40px;
width:60px;
display:flex;
justify-content: center;
align-items: center;
}

.submit-button{
height:40px;
width:60px;
display:flex;
justify-content: center;
align-items: center;
}

.search-div-book{
height:auto;
display:flex;
justify-content:center;
align-items:center;
width:100%
}
.search-book-list{
width:800px;
display:flex;
  flex-wrap:wrap ;
}
.search-book-container{
  display:flex;
  width:240px;
  height:240px;
  flex-direction:column;
  border-radius: 10px;
  background-color:#fafafa;
  margin:10px;

}
.search-book-show{
  width:100%;
  height:40px;
   display:flex;
}
.search-book-showleft{
width:70px;
height:100%;
padding-left:10px;
display:flex;
justify-content:center;
align-items:center;
}
.search-book-showright{
width:140px;
height:100%;
padding-left:10px;
display:flex;
justify-content:center;
align-items:center;
}

</style>